.Home {
  block-size: calc(100vh - 12rem);
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  text-align: center;

  &__greet {
    font-size: 4rem;
    font-weight: bold;
    color: #a5a5a5;
  }
  &__name {
    font-size: 6rem;
    font-weight: bold;
  }
  &__role {
    font-size: 2rem;
    font-weight: 300;
    margin-block-start: 1rem;
  }
  &__tagLine {
    margin-block-start: 4rem;
    font-size: 1.5rem;
    max-inline-size: 30em;
    line-height: 2.5rem;
    color: #8a8a8a;
  }
}
